






























































































































































	import { Component, Vue } from "vue-property-decorator";
	import { Route } from 'vue-router';
	import OrderSummary from '@/components/orderSummary.vue';
	import { loadStripe } from '@stripe/stripe-js';

	Component.registerHooks(['beforeRouteEnter']);

	@Component({components:{ OrderSummary }})
	export default class Cart extends Vue {
		currentSection: String = 'summary';
		contact: any = {};
		shipping: any = {};
		stripe: any = null;
        is_processing: boolean = false;
        is_paid: boolean = false;
		product: any = {};
		payment_error = false;
		user = {name:'', email:'', address:''};

        card: any = null;
        cardErrors: HTMLElement = null;

		mounted() { this.init(); }

		async init() {
			this.stripe = await loadStripe('pk_test_7b7WnoyAoB1d8jL0yAijQfjV');
			let elements = this.stripe.elements();
            this.card = elements.create('card');
            this.card.mount('#card-element');
            this.card.on('change', ({error}) => {
                this.cardErrors = document.getElementById('card-errors');
                if(error){ this.cardErrors.textContent = error.message;
                }else{ this.cardErrors.textContent = '';}
            });
		}

        private async getPaymentToken() {
			this.is_processing = true;
            let { token, error } = await this.stripe.createToken(this.card);
            if(error){ 
				this.is_processing = false;
				this.cardErrors.textContent = error.message;
            }else{ this.paymentTokenHandler(token); }
        }

        private async paymentTokenHandler(token) {
            const url = 'http://192.168.1.252:3000/process-payment';
			const headers = {"Content-Type": "application/json"};
			var d = JSON.stringify({
				token: token.id, amount: this.$store.state.cart.subTotal,
				...this.user
			});
            fetch(url, { method: 'post', body: d, headers, mode:'cors' })
                .then(response => response.json())
				.then(data => { 
					if(data['paid']) {
						this.is_processing = false; this.is_paid = true; this.payment_error = false;
						setTimeout(() =>this.$store.dispatch('EMPTY_CART'), 1000);	
					}else{ this.is_processing = false; this.is_paid = false; this.payment_error = true;}
				});
		}
		
		private deleteProduct(title) {
			this.$store.dispatch('REMOVE_FROM_CART', { product: title });
		}
}
